.imgset img
{
   width: 200px;
   max-height: 200px;
   justify-content: center;
   margin-left: 30px;

}
.loadingg
{
  margin-left: 600px;
  margin-top: 200px;
  height: 200px;
  width: 400px;
  background-color: orange;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.loading2
{
  
  padding-top: 100px;
  margin-left: 100px;
}

p{
  margin-left: 30px;
}
.title{
  text-decoration: underline;
  font-size: 22px;
  color: black;
  font-weight: bold;
}
::placeholder{
  font-size: 20px;
}


.mapp{
  display: grid;
  justify-content: space-around;
  grid-template-columns: 15% 15% 15% 15%;
  margin-top:50px;

  
}
.maindiv1:hover{
  background-color: grey;
}

.maindiv1{

  margin-top: 20px;
  border: 2px solid black;
  height: 300px;
}
.inputfield img{
  width: 100%;
  height: 700px;
  -webkit-backface-visibility: hidden;
  -ms-transform: translateZ(0); /* IE 9 */
  -webkit-transform: translateZ(0); /* Chrome, Safari, Opera */
  transform: translateZ(0);
}
input{
  width: 200px;
  height: 35px;
  border: 2px solid black;
  border-radius: 6px;
  position: absolute;
  justify-content: center;
  left:740px;
  top: 650px;
}


@media ( max-width:800px)
{

  .loadingg{
    margin-left: -10px;
  }
  .mapp{
    display: block;
    justify-content: center;
    margin-left: 20px;
    width: 350px;
  
  }
  .imgset img{
    width: 300px;
   min-height: 350px;
  }
  .maindiv1{
    height: 500px;
    margin-left: -20px;
  }
  input{
    position: absolute;
    left: 100px;
  }

  .inputfield img{
    width: 100%;
    height: 700px;
 
  }
} 