.main{
    margin-left: 300px;
    margin-top: 150px;
    background-color: orange;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    height: 800px;
    width: 500px;
    }
    
    .btns{
        margin-left: 50px;
        width: 130px;
        height: 50px;
        border: 2px solid white ;
        border-radius: 20px;
        font-size: 20px
    }
    
    
    .main2{
        /* margin-top: 100px; */
        padding-top: 50px;
        margin-left: 100px;
    }
    
    .sp1,.sp2,.sp3,.sp4{
        color: cornflowerblue;
    }
    .h11,.h22,.h33,.h44{
        color: darkcyan;
    }
    
    
    @media (max-width:998px)
    {
        .main
        {
            margin-left: 10px;
            width: 350px;
            height: 400px;
        }
        .main2{
            margin-left: 50px;
        }
        
    
        .h11,.h22,.h33,.h44{
            font-size: 25px;
        }
    
    
    }